.container {
    display: flex;
    flex-direction: column; /* чтобы элементы шли сверху вниз */
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding: 0 20px;
    text-align: center;
  }
  


.title {
    font-size: 40px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 20px;
    color: #2c3e50;
  }
  
  .text {
    font-size: 20px;
    text-align: center;
    color: #555;
    max-width: 600px;
    margin: 0 auto;
  }

  .text a {
    color: #3498db;
    text-decoration: none;
    font-weight: 500;
  }
  
  .text a:hover {
    text-decoration: underline;
  }
  
  @media (max-width: 480px) {
    .title {
      font-size: 28px;
    }
  
    .text {
      font-size: 16px;
    }
  }
  